import { useState } from "react";
import onSubmitForm from "../Api/sendMessage";
import Cleave from "cleave.js/react";

const Contact = ({ activeContact }) => {
  const [numberInput, setNumberInput] = useState("+998 ");
  const [nameInput, setNameInput] = useState("");
  const [nameInputValid, setNameInputValid] = useState({
    valid: false,
    value: "",
    sendValid: "",
  });
  const [numberInputValid, setNumberInputValid] = useState({
    valid: false,
    value: "",
    sendValid: "",
  });

  const onHandleChangeInput = (e) => {
    if (e.target.name === "numberInput") {
      setNumberInput(e.target.value);
      onBlurValidNumber(e);
    } else if (e.target.name === "nameInput") {
      setNameInput(e.target.value);
      onBlurValidName(e);
    }
  };

  const clearInput = () => {
    setNumberInput("+998 ");
    setNameInput("");
    setNameInputValid({
      valid: false,
      value: "",
      sendValid: "",
    });
    setNumberInputValid({
      valid: false,
      value: "",
      sendValid: "",
    });
  };

  const onBlurValidName = (e) => {
    const nameValue = e.target.value;
    const target = e.target.style;

    if (/\d/gi.test(nameValue)) {
      setNameInputValid({
        valid: true,
        value: "Son yozmay ism yozing!",
        sendValid: false,
      });
      target.outline = "2px solid red";
    } else if (nameValue.length === 0) {
      setNameInputValid({
        valid: true,
        value: "Ismingizni yozing!",
        sendValid: false,
      });
      target.outline = "2px solid red";
    } else if (/\w/gi.test(nameValue)) {
      const arr = nameValue.match(/\w/gi);
      const value1 = arr[0];
      let count = 0;
      arr.forEach((item) => {
        if (value1.toLowerCase() === item.toLowerCase()) {
          count++;
        }
      });
      if (arr.length === count || nameValue.length < 3) {
        setNameInputValid({
          valid: true,
          value: "Iltimos ismingizni yozing!",
          sendValid: false,
        });
        target.outline = "2px solid red";
      } else {
        setNameInputValid({ valid: false, value: "", sendValid: true });
        target.outline = "none";
      }
    }
  };

  const onBlurValidNumber = (e) => {
    const nameValue = e.target.value;
    const target = e.target.style;
    if (nameValue.length !== 19) {
      setNumberInputValid({
        valid: true,
        value: "Raqamni to'liq yozing!",
        sendValid: false,
      });
      target.outline = "2px solid red";
    } else {
      setNumberInputValid({ valid: false, value: "", sendValid: true });
      target.outline = "none";
    }
  };

  const onClickButton = (e) => {
    const name = e.target.form[0];
    const number = e.target.form[1];
    const btn = e.target.form[2];
    if (!nameInputValid.sendValid) {
      name.style.outline = "2px solid red";
      setNameInputValid({
        valid: true,
        value: "Ismingizni yozing!",
        sendValid: false,
      });
    } else name.style.outline = "none";

    if (!numberInputValid.sendValid) {
      number.style.outline = "2px solid red";
      setNumberInputValid({
        valid: true,
        value: "Raqamni to'liq yozing!",
        sendValid: false,
      });
    } else number.style.outline = "none";

    if (!nameInputValid.sendValid) {
      name.focus();
    } else if (!numberInputValid.sendValid) {
      number.focus();
    } else {
      btn.focus();
    }
  };

  return (
    <section
      className={`contact container ${
        activeContact ? "contact-click-style" : ""
      }`}
    >
      <h2 data-aos="fade-right" data-aos-duration="600" data-aos-once="true">
        Aloqa
      </h2>
      <div
        className="form"
        data-aos="zoom-in-down"
        data-aos-duration="800"
        data-aos-once="true"
      >
        <div
          className="form-text"
          data-aos="fade-right"
          data-aos-duration="600"
          data-aos-delay="450"
          data-aos-once="true"
        >
          <h2>Quyidagi formani to’ldiring va biz siz bilan bog’lanamiz</h2>
          <p>
            Bizda o’qib zamonaviy kasblarni o’rganing va ilk millionlaringizni
            ishlashni boshlang.
          </p>
        </div>
        <div
          className="form-box"
          data-aos="fade-left"
          data-aos-duration="600"
          data-aos-delay="450"
          data-aos-once="true"
        >
          <form
            onSubmit={(e) =>
              onSubmitForm(
                e,
                null,
                clearInput,
                nameInputValid.sendValid && numberInputValid.sendValid
              )
            }
          >
            <div>
              <label htmlFor="nameInputC">
                <input
                  type="text"
                  name="nameInput"
                  placeholder="Ism va familyangiz"
                  autoComplete="off"
                  value={nameInput}
                  onChange={onHandleChangeInput}
                  id="nameInputC"
                  onBlur={onBlurValidName}
                />
              </label>
              {nameInputValid.valid ? <span>{nameInputValid.value}</span> : ""}
            </div>
            <div>
              <label htmlFor="numberInputC">
                <Cleave
                  options={{
                    blocks: [4, 0, 2, 0, 3, 2, 2],
                    delimiters: [" ", "(", ")", " ", "-"],
                    numericOnly: true,
                    prefix: "+998",
                  }}
                  value={numberInput}
                  onChange={onHandleChangeInput}
                  name="numberInput"
                  autoComplete="off"
                  type="tel"
                  id="numberInputC"
                  onBlur={onBlurValidNumber}
                />
              </label>
              {numberInputValid.valid ? (
                <span>{numberInputValid.value}</span>
              ) : (
                ""
              )}
            </div>
            <div>
              <button onClick={onClickButton} type="submit">
                Yuborish
              </button>
            </div>
          </form>
        </div>
      </div>
    </section>
  );
};

export default Contact;
